
import { defineComponent, PropType, ref, watch } from 'vue'

import { IformItem } from '@/base-ui/form/index'

export default defineComponent({
  props: {
    // field是后期添加的唯一标识
    // v-model = "formData[`${item.field}`]"
    modelValue: {
      type: Object,
      required: true
    },
    formItems: {
      // es6语法
      type: Array as PropType<IformItem[]>,
      // 当有默认类型时是对象或者数组时需要写成箭头函数得形式
      default: () => []
    },
    labelWidth: {
      type: String,
      default: '90px'
    },
    itemStyle: {
      type: Object,
      default: () => ({ padding: '10px 40px' })
    },
    colLayout: {
      type: Object,
      default: () => ({
        xl: 6, //>=1920px显示24/6=4个
        lg: 8, //>=1200px --> 3
        md: 12, //>=992px --> 2
        sm: 24, //>=768px --> 1
        xs: 24 //<768px --> 1
      })
    }
  },
  emits: ['update:modelValue'],
  setup(props, { emit }) {
    // 此时的formData是一个解构出来的新的对象(浅拷贝)，而且通过ref使其成为了一个响应式的数据
    const formData = ref({ ...props.modelValue })
    watch(
      // 监听formData的变化，然后将事件以及新的值发射到外部绑定的组件中
      formData,
      (newValue) => {
        emit('update:modelValue', newValue)
      },
      { deep: true }
    )

    // 以上方法在pageSearch、HxForm、单个数据 之间的数据传递都使用的双向绑定，这使得处理起来会很麻烦
    // 另外一种处理重置的思路：不使用v-model。使用原生的值绑定和事件绑定
    // :modelValue ="modelValue[`${item.field}`]"
    // @update:modelValue="handleValueChange($event,item.field)"

    // const handleValueChange = (value:any,field:string)=>{
    //emit('update:modelVaule',{...props.modelValue,[field]:value})
    // }

    // pageSearch处理事件发射：const handleResetClick = ()=>{formData.value = searchFormData}

    return {
      formData
    }
  }
})
