
import { defineComponent, computed, ref, watch } from 'vue'

import { useStore } from '@/store'
import HxTable from '@/base-ui/table'

import { usePermission } from '@/hooks/usePermission'

export default defineComponent({
  components: {
    HxTable
  },
  props: {
    contentTableConfig: {
      type: Object,
      required: true
    },
    pageName: {
      type: String,
      required: true
    }
  },
  emits: ['newClick', 'editClick'],
  // setup类似于created，只会调用一次
  setup(props, { emit }) {
    const store = useStore()

    // 获取按钮的权限
    const isCreate = usePermission(props.pageName, 'create')
    const isUpdate = usePermission(props.pageName, 'update')
    const isDelete = usePermission(props.pageName, 'delete')
    const isQuery = usePermission(props.pageName, 'query')

    // 1.分页后重新请求数据
    const pageInfo = ref({ currentPage: 1, pageSize: 10 })
    watch(pageInfo, () => getPageData())

    // 2.发送网络请求
    const getPageData = (queryInfo: any = {}) => {
      if (!isQuery) return
      store.dispatch('systemModule/getPageListAction', {
        pageName: props.pageName,
        queryInfo: {
          offset:
            pageInfo.value.currentPage * pageInfo.value.pageSize -
            pageInfo.value.pageSize,
          size: pageInfo.value.pageSize,
          ...queryInfo
        }
      })
    }
    getPageData()
    // 3.从vuex中获取数据
    const dataList = computed(() =>
      // 返回值是一个函数，而且需要传入参数
      store.getters[`systemModule/pageListData`](props.pageName)
    )
    // 用于分页
    const dataCount = computed(() =>
      store.getters[`systemModule/pageListCount`](props.pageName)
    )

    // 4.获取其他的动态插槽名称
    const otherPropsSlots = props.contentTableConfig.propList.filter(
      (item: any) => {
        if (item.slotName === 'createAt') return false
        if (item.slotName === 'updateAt') return false
        if (item.slotName === 'handle') return false
        return true
      }
    )
    // 删除数据：发射事件到store--》store发射删除的网络请求
    const handleDeleteClick = (item: any) => {
      console.log(item)
      store.dispatch('systemModule/deleteDataAction', {
        pageName: props.pageName,
        id: item.id
      })
    }
    const handleNewClick = () => {
      emit('newClick')
    }
    const handleEditClick = (item: any) => {
      emit('editClick', item)
    }

    return {
      dataList,
      dataCount,
      getPageData,
      pageInfo,
      otherPropsSlots,
      isCreate,
      isUpdate,
      isDelete,
      handleDeleteClick,
      handleNewClick,
      handleEditClick
    }
  }
})
